import { UserService } from 'src/app/services/user.service';
import { Observable } from 'rxjs';
import { AuthService } from './../../services/auth.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoadingController, NavController, ToastController } from '@ionic/angular';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { User } from 'src/app/models/user.model';
import { take     } from 'rxjs/operators';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  img = 'assets/images/logo_new.png'
  form: FormGroup;
  constructor(
    private router: Router,
    private navCtrl: NavController,
    private authService: AuthService,
    private loadCtrl: LoadingController,
    private toastCtrl: ToastController,
    private userSrv: UserService
  ) { }

  ngOnInit() {
    this.initialFormLogin()
  }

  initialFormLogin(){
    this.form = new FormGroup({
      email:    new FormControl(null,[Validators.required]),
      password: new FormControl(null,[Validators.required])
    });
  }

  onRegister() {
    this.router.navigate(['register']);
  }

  onHome() {
    this.router.navigate(['anamnese']);
  }

  onClick() {

  }

  async submitLogin() {
    const loading = await this.loadCtrl.create({ message: 'Carregando' });
    try {

      loading.present();
  
      const response: User = await this.authService.login(this.form.value).toPromise();
  
      this.authService.checkAnamneseUser().subscribe((user) => {
        if (user.check === true) {
          this.router.navigate(['tabs']);
        } else {
          this.router.navigate(['anamnese']);
        }
        loading.dismiss();
      });
  
    } catch (err) {
      const toast = await this.toastCtrl.create({
        message: 'Falha ao tentar logar',
        duration: 3000,
      });
      toast.present();
      loading.dismiss();
    }
  }
  
  mostrarSenha() {
    const inputPass = document.getElementById('senha') as HTMLInputElement;
    const btnShow = document.getElementById('btn-senha') as HTMLButtonElement;
  
    if (inputPass.type === 'password') {
      inputPass.setAttribute('type', 'text');
      btnShow.setAttribute('name', 'eye-outline');
    } else {
      inputPass.setAttribute('type', 'password');
      btnShow.setAttribute('name', 'eye-off-outline');
    }
  }

}
